import axios from 'axios';
import {notification} from 'antd';

const axiosInstance = axios.create({
    timeout: 10 * 1000,
    withCredentials: true,
    headers: {
        'Cache-Control': 'no-cache',
        'Content-Type': 'application/json'
    }
});

const parseResponseData = (data: any) => {
    if (typeof data === 'string') {
        try {
            return JSON.parse(data);
        } catch (e) {
            return data;
        }
    }
    return data;
};

axiosInstance.interceptors.response.use(
    response => {
        const result = parseResponseData(response.data);
        const { error, message, data } = result;
        // 表示有异常
        if (error !== 0) {
            if (message) {
                console.log(message)
            }
            return Promise.reject(new Error(message || '服务器开小差了~'));
        }
        return data;
    },
    error => {
        if (error.response && error.response.status === 401) {
            return;
        }
        if (error.response && error.response.status) {
            notification.error({message: '服务器开小差了~'})
            return Promise.reject(new Error(`服务器 ${error.response.status} 异常`));
        }
        return Promise.reject(error);
    }
);

export default axiosInstance;
