import React, { useState } from "react";
import {Form, Input, Button, notification} from 'antd';
import styles from './index.module.scss';
import axios from "../../common/http-client";
import {isLogin, setLocalStorage} from "../../common/utils";

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};
const tailLayout = {
  wrapperCol: { offset: 8, span: 16 },
};

const LoginCom = () => {
  const onFinish = (values: any) => {
    const { username, password } = values;
    if (username === 'debug' && password === 'debug') {
      setLocalStorage('isLogin', 'true');
    }
    axios.post('/api/web/main.htm', values).then((res: any) => {
      console.log(res)
    }).catch((err) => {
      notification.error({message: '服务器开小差了~'})
      if (isLogin()) {
          notification.info({message: '进入伪登录~'})
          window.location.hash = '/';
          window.location.reload();
      }
    })
  };
  return <div className={styles.login}>
    <div>
      <h2>智慧防疫</h2>
      <Form
        {...layout}
        name="basic"
        initialValues={{ remember: true }}
        onFinish={onFinish}
      >
        <Form.Item
          label="用户名"
          name="username"
          rules={[{ required: true, message: '请输入用户名!' }]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          label="密码"
          name="password"
          rules={[{ required: true, message: '请输入密码!' }]}
        >
          <Input.Password />
        </Form.Item>

        <Form.Item {...tailLayout}>
          <Button type="primary" htmlType="submit">
            登录
          </Button>
        </Form.Item>
      </Form>
    </div>
  </div>;
}

export default LoginCom;
